.container3 {
  position: relative;
  padding: 120px 20px 20px; /* Adjusted padding to create space for the logo */
  height: 100vh;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../Images/d3.jpg');
  background-size: cover;
  background-position: center;
  filter: blur(10px);
}

.logo {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 100px;
  height: auto;
}

.content {
  position: relative;
  z-index: 1;
  margin-top: -100px; /* Adjusted margin-top to move the content up */
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 8px;
}

th {
  font-weight: bold;
  text-align: left;
}

button {
  background-color: #ca428d;
  color: white;
  border: none;
  font-size: 14px; 
  padding: 6px 12px; 
  cursor: pointer;
  border-radius: 4px;
  width: 100px;
  height: 35px;
  
}

button.add-venue {
  font-size: 16px; 
  width: 200px;
  height: 35px;
  font-family: sans-serif;
}

a {
  text-decoration: none;
  color: #ca428d;
}

a:hover {
  text-decoration: underline;
}
