body {
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
    margin: 0;
    padding: 0;
}

.wrapper-success {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.payment-success {
    text-align: center;
    padding: 20px;
}

.payment-success h2 {
    color: #4CAF50; /* Green color for success message */
}

.payment-success p {
    font-size: 18px;
    margin-top: 10px;
}

/* Review form styles */
.review-form {
    text-align: center;
    margin-top: 20px;
}

.review-form textarea {
    width: 100%;
    height: 100px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 10px;
}

.review-form .star-rating {
    text-align: center;
    margin-top: 20px;
}

.review-form .star-rating input[type="checkbox"] {
    display: none;
}

.review-form .star-rating label {
    font-size: 24px;
    color: #ccc; /* Star color when not selected */
    cursor: pointer;
}

.review-form .star-rating input[type="checkbox"]:checked + label {
    color: #ff9800; /* Star color when selected */
}

.review-form button {
    background-color: #4CAF50;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}
.return-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #ca428d;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bold;
    margin-top: 20px;
    transition: background-color 0.3s ease;
}
