/* Beachfront.css file */

.container2 {
  text-align: center;
  margin-bottom: 20px; /* Add margin to separate each container2 */
}

.venue-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 1000px;
  align-items: center; /* Center the container horizontally */
}

.venue-item {
  display: flex;
  margin-bottom: 20px;
  width: 1000px; /* Adjust the max-width as needed */
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.image-container {
  flex-shrink: 0;
}

.venue-image {
  max-width: 400px; /* Adjust the max-width for a larger image */
  max-height: 400px; /* Adjust the max-height for a larger image */
  object-fit: cover;
}

.details-container {
  flex: 1;
  padding: 20px;
  position: relative;
}


.book-now-button {
  text-decoration: none;
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 100px;
  background-color: #c24994;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.book-now-button :hover{
  text-decoration: none;
}