/* Booking.css */

.booking-page-container {
    position: relative;
  }
  
  .logo-container {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1;
  }
  
  .logo {
    width: 100px;
    height: auto;
  }
  
  .booking-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-image: url('../Images/d3.jpg');
    background-size: cover;
    background-position: center;
    position: relative;
    color: white;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  }
  
  .booking-form {
    /* background-color: rgba(255, 255, 255, 0.7); Semi-transparent white background for the form */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3); /* 3D box shadow effect */
    z-index: 0;
  }
  
  label {
    margin: 10px 0;
    color: black;
  }
  
  input {
    padding: 8px;
    margin-top: 5px;
  }
  
  button {
    background-color: #c24994;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 15px;
  }
  