.wrapper1 {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.background-blur {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../Images/a5.jpg');
  background-size: cover;
  background-position: center;
  filter: blur(5px); /* Add a blur filter to the background image */
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5);
}

.sidelogo {
  position: absolute;
  top: 20px;
  left: 20px;
}

.sidebar {
  text-align: center;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.sidebar li {
  margin: 10px;
  flex: 0 0 calc(33.33% - 20px); /* Adjusted width for three cards in a row with margin */
  max-width: calc(33.33% - 20px); /* Adjusted max-width for responsive design with margin */
}

.sidebar a {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: black;
  transition: background-color 0.3s ease;
  font-family: 'Sofia', sans-serif; 
  font-size: 25px; 
  font-weight: bold;
}
.sidebar a:hover{
  text-decoration: none;
}

.sidebar svg {
  margin-right: 10px;
}

.sidebar .card1 {
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
  padding: 20px;
  width: 80%; /* Adjusted width for the card */
  height: 100px;
}

.sidebar .card1:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.dashboard-link {
  font-family: 'Sofia', sans-serif;
  font-size: 25px;
  font-weight: bold;
}
