.message-item {
    border: 1px solid #fffcfc;
    padding: 10px;
    margin-bottom: 10px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2); 
}

.message-button {
    cursor: pointer;
    width: 105px;
    background-color: #ca428d;
    color: white;
    border: none;
    padding: 5px 5px;
    margin-top: 5px;
    border-radius: 3px;
}
