.dashboard-background {
  position: relative;
  width: 100vw; /* Set the width and height of the container to fill the entire viewport */
  height: 100vh;
}

.background-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.dashboard-heading {
  color: black;
  text-align: center;
  position: relative;
  z-index: 1;
  font-family: 'Sofia', sans-serif;
  font-size: 25px;
}

.dashboard-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.card2 {
  border-radius: 8px;
  padding: 20px;
  margin: 10px;
  width: 200px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card2 h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.card2 p {
  font-size: 18px;
  font-weight: bold;
}
